.container_nav_bar{
    display: flex ;
    flex-direction: column;
    position: relative;
    background-color: red;
   
  }
  .Hnav_bloc{
      display: flex;
      justify-content: space-between;
      position: absolute;
      z-index: 2;
      width: 100%;
  }
  .GPlien_nav_bar{
      display: none;
  }
  /*/MENU  carousel /*/
  .classname_caroussel{
