  /*/MENU  A DROITE /*/
  
  
  .Dlien_con_inscr{
      text-decoration: none;
      display: flex;
      justify-content: center;
      align-items: center;
      color: black;
      margin-right: 1rem;
  }
  
  .Dlien_nav_bar_connexion{
      margin-right: 0.5rem;
      font-size: 1.2rem;
      font-weight: 700;
      color: white;
      display: flex;
      width: 9rem;
   }
  
  .Dlien_nav_bar_inscrire{
      margin-right: 0.5rem;
      font-size: 1rem;
      font-weight: 700;
      color: white;
      border: solid 1px aliceblue;
      border-radius:  5px;
      padding: 0.3rem;
      padding-left: 1rem;
      padding-right: 1rem;
  }
  
  .Dlien_nav_bar_inscrire:hover{
