  }
  /*/----------------------/*/
  
  .blc_form_conn{
      position: absolute;
      z-index: 3;
      width: 100%;
      height: 100vh;
  }
  
  .cont_form_connection {
        background-color: rgba(0, 128, 0, 0.329);
        width: 100%;
        height: 100%;
  }
  
  .bloc_connection {
       position: absolute;
       z-index: 4;
       top: 50%;
       left: 50%;
       transform: translate(-50%, -50%);
  }
  .GGlien_nav_bar_no{
    text-decoration: none;
    display: flex;
    padding: 1rem;
    align-items: flex-start;
    color: #002244;
    justify-content: flex-end;
    margin-left: 1rem;
}

  .GGlien_nav_bar_accueil_no{
    text-transform: uppercase;
    font-size: 1.2rem;
    font-weight: 700;
    color: aliceblue;
 }
 

  .log_avec_accueil_no{
    height: 10px;
    width: 10px;
