    border: solid 2px aliceblue;
}

/*/  CHERCHE  et PAS DE COMME LES AUTRES  AJOUT /*/
 .MR_search {
 
   
  }
  
  .MR_search > div {
    display: inline-block;
    position: relative;
    filter: drop-shadow(0 1px rgba(0, 0, 0, 0.473));
  }
  
  .MR_search > div:after {
    content: "";
    background: rgba(0, 0, 0, 0.473);
    width: 4px;
    height: 20px;
    position: absolute;
    top: 20px;
    right: -7px;
    transform: rotate(125deg);
  }
  
  .MR_search > div > input {
    color: rgba(0, 0, 0, 0.473);
    font-size: 10px;
    background: transparent;
    width: 15px;
    height: 15px;
    padding: 5px;
    border: solid 3px rgba(0, 0, 0, 0.473);
    outline: none;
    border-radius: 35px;
    transition: width 0.5s;
  }
  
  .MR_search > div > input::placeholder {
    color: rgba(0, 0, 0, 0.473);
    opacity: 0;
    transition: opacity 150ms ease-out;
  }
  
  .MR_search > div > input:focus::placeholder {
    opacity: 1;
  }
  
  .MR_search > div > input:focus,
  .MR_search > div > input:not(:placeholder-shown) {
    width: 250px;
  }
  

@media screen and (max-width : 700px){
  .MR_radio_contai{
