.cont_form_connecxion{
    border-radius: 7px;
    background-color: rgb(216, 222, 226);
    width: 400px;
    height: 450px;
    box-shadow: 0 1px 1px rgba(0,0,0,0.08), 0 2px 2px rgba(0,0,0,0.12), 0 4px 4px rgba(0,0,0,0.16), 0 8px 8px rgba(0,0,0,0.20);
}
.cont_form_mail{
    border-radius: 7px;
    background-color: rgb(216, 222, 226);
    width: 400px;
    height: 250px;
    box-shadow: 0 1px 1px rgba(0,0,0,0.08), 0 2px 2px rgba(0,0,0,0.12), 0 4px 4px rgba(0,0,0,0.16), 0 8px 8px rgba(0,0,0,0.20);
}
.cont_form_change_pass{
    border-radius: 7px;
    background-color: rgb(216, 222, 226);
    width: 400px;
    height: 300px;
    box-shadow: 0 1px 1px rgba(0,0,0,0.08), 0 2px 2px rgba(0,0,0,0.12), 0 4px 4px rgba(0,0,0,0.16), 0 8px 8px rgba(0,0,0,0.20);
}

.cont_form_confirme_pass{
    position: absolute;
    border-radius: 7px;
    background-color: rgb(216, 222, 226);
    width: 400px;
    height: 300px;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    box-shadow: 0 1px 1px rgba(0,0,0,0.08), 0 2px 2px rgba(0,0,0,0.12), 0 4px 4px rgba(0,0,0,0.16), 0 8px 8px rgba(0,0,0,0.20);
}
