.container_footer{
    display: flex;
    flex-basis: 100%;
    padding: 3rem;

}

.cont_rail_total{
    display: flex;
    justify-content: space-between;
    width: 100%;
}
.cont_rail{
  display: flex;
  flex-direction: column;
}
.cont_rail_to{
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: flex-end;
}
.cont_rail1{
    width: 6vw;
    height: 4px;
    background-color: #C81D11;
    margin: 0.4rem;
}
.cont_rail2{
    width: 13vw;
    height: 4px;
    background-color: #C81D11;
    margin: 0.4rem;
}
.cont_rail3{
    width: 30vw;
    height: 4px;
    background-color: #C81D11;
    margin: 0.4rem;
}
.text_footer{
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 2.2rem;
    letter-spacing: 2rem;
    color: #00224494;
    
}
@media screen and (max-width : 875px){

    .text_footer{
        display: flex;
        justify-content: center;
        align-items: flex-end;
        font-size: 1rem;
        letter-spacing: 0rem;
    }
}