.container_acceil{
  width: 70vw;
  margin: auto;
  margin-top: 10vh;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  border-top: 1px solid rgba(240, 248, 255, 0.349);
  border-bottom: 1px solid rgba(240, 248, 255, 0.349);
}
.cont_bulle{
    height: 9vw;
    width: 9vw;
    border-radius: 100%;
    margin: 2rem;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 8px solid #C81D11;
    color: #002244;
    background-color: #f1f7fd18;
    text-decoration: none;
    box-shadow: rgba(20, 112, 182, 0.3) 0px 1px 2px 0px, rgba(12, 99, 165, 0.15) 0px 1px 3px 1px;
}

.span_bul{
  font-size: 1.2rem;
  font-weight: bold;
  color: #04203b91;
}

.span_bul:hover{
  color: #002244;
}

@media screen and (max-width : 1400px){

  .cont_bulle{
    height: 200px;
    width:   200px;
  }

}

