/*/  CHERCHE  et AJOUT /*/
.FR_inp_rech{
   width: 70vw;
   display: flex;
   align-items: center;
   margin: 20px;
   justify-content: space-between;   
}

.FR_btn_ad{
   display: flex;
   flex-direction: column;
   justify-content: center;
   align-items: center;
}
