   height: 50px;
   width : 50px;
   border-radius: 100%;
   border: solid 5px firebrick;
   display: flex;
   justify-content: center;
   align-items: center;
}
.FR_plus_ajout{
   color: rgba(178, 34, 34, 0.651);
   font-size: 1.4rem;
   
}
.FR_ajout{
   color: rgba(178, 34, 34, 0.651);
}
.FR_search {

  
 }
 
 .FR_search > div {
   display: inline-block;
   position: relative;
   filter: drop-shadow(0 1px rgba(0, 0, 0, 0.473));
 }
 
 .FR_search > div:after {
   content: "";
   background: rgba(0, 0, 0, 0.473);
   width: 4px;
   height: 20px;
   position: absolute;
   top: 20px;
   right: -7px;
   transform: rotate(125deg);
 }
 
 .FR_search > div > input {
   color: rgba(0, 0, 0, 0.473);
   font-size: 10px;
   background: transparent;
   width: 15px;
   height: 15px;
   padding: 5px;
   border: solid 3px rgba(0, 0, 0, 0.473);
   outline: none;
   border-radius: 35px;
   transition: width 0.5s;
 }
 
 .FR_search > div > input::placeholder {
   color: rgba(0, 0, 0, 0.473);
   opacity: 0;
   transition: opacity 150ms ease-out;
 }
 
 .FR_search > div > input:focus::placeholder {
   opacity: 1;
 }
 
 .FR_search > div > input:focus,
 .FR_search > div > input:not(:placeholder-shown) {
   width: 250px;
 }
 
 .FR_delete{
   display: flex;
   justify-content: flex-end;
   font-size: 1.3rem;
   color: aliceblue;
}

.FR_delete:hover{
   color: rgba(176, 190, 202, 0.836);
}
 @media screen and (max-width : 1180px){

