}

.ST_btn_radio{
  position: absolute;
  right: 15%;
  top: 17%;
  display: flex;
  padding: 2rem;
}

.ST_radio_contai{
   width: 120px;
   display: flex;
   flex-direction: column;
}
.ST_radio_inp{
   margin: 0.2rem;
   display: flex;
   justify-content: space-between;
}
.ST_name_label{
   color: rgb(87, 75, 75);
   font-size: 1.2rem;
}
.ST_search {

  
 }
 
 .ST_search > div {
   display: inline-block;
   position: relative;
   filter: drop-shadow(0 1px rgba(0, 0, 0, 0.473));
 }
 
 .ST_search > div:after {
   content: "";
   background: rgba(0, 0, 0, 0.473);
   width: 4px;
   height: 20px;
   position: absolute;
   top: 20px;
   right: -7px;
   transform: rotate(125deg);
 }
 
 .ST_search > div > input {
   color: rgba(0, 0, 0, 0.473);
   font-size: 10px;
   background: transparent;
   width: 15px;
   height: 15px;
   padding: 5px;
   border: solid 3px rgba(0, 0, 0, 0.473);
   outline: none;
   border-radius: 35px;
   transition: width 0.5s;
 }
 
 .ST_search > div > input::placeholder {
   color: rgba(0, 0, 0, 0.473);
   opacity: 0;
   transition: opacity 150ms ease-out;
 }
 
 .ST_search > div > input:focus::placeholder {
   opacity: 1;
 }
 
 .ST_search > div > input:focus,
 .ST_search > div > input:not(:placeholder-shown) {
   width: 250px;

 }
 
 @media screen and (max-width : 1180px){
   .ST_inp_rech{
       width: 65vw;
       display: flex;
       flex-direction: column;
       align-items: flex-end;
       margin: 20px;
       justify-content: space-between;
       position: relative;
   }
   .ST_btn_ad{
       display: flex;
       flex-direction: row;
       justify-content: center;
       align-items: center;
       margin-right: 2rem;
       margin-left: 50vw;
       margin-bottom: 1rem;
       position: absolute;
       top : -30% ;
       left: -90%;
  
   }
   .ST_add_cont{
      height: 30px;
      width : 30px;
      border-radius: 100%;
      border: solid 5px firebrick;
      display: flex;
      justify-content: center;
      align-items: center;
   }
   .ST_ajout{
       color: rgba(178, 34, 34, 0.651);
       margin-left: 1rem;
       font-size: 0.6rem;
   }
   .ST_btn_radio{
      position: absolute;
      right: 0%;
      top: 40px;
      display: flex;
      padding-top:0.5rem;
    }
    .ST_search > div > input:focus,
    .ST_search > div > input:not(:placeholder-shown) {
      width: 150px;
   
    }

    .ST_name_label{
   color: rgb(87, 75, 75);
   font-size: 0.8rem;
}
}


