    width: 30%;
    height: 30%;
}

.carousel_img{
   
}


/*/--------/*/
.carousel_text{
    display: flex;
    flex-direction: column;
    text-align: start;
    width: 500px;
}

.carousel_text_1{
    font-size: 3rem;
    line-height: 1em;
    color: white;
    font-weight: bold;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

.lien_contact{
    display: flex;
    align-items: center;
}
 
.nom_lien_contact{
    color: aliceblue ;
    font-size: 1rem;
}

.lien_icon_contact{
    margin: 1rem;
    display: flex;
    justify-content: center;
    padding: 0.2rem;
    border:  solid 1px white;
    border-radius: 2rem;
    padding-left:  0.6rem;;
    padding-right:  0.6rem;
}

.lien_icon_contact:hover{
    background-color: rgba(240, 248, 255, 0.384);
    color: red;
}

.ico_cont{
    font-size: 1rem;
    margin-right: 0.5rem;
    color: aliceblue;
}

.lien_cont_carou{
    text-decoration: none;
    color: aliceblue;
    font-size: 0.7rem;
}

.carousel_text_img_slide{
    position: absolute;
    top: 20%;
    left: 20%;
    width: 60%;
    right: 20%;
     display: flex;
     justify-content: space-between;
     align-items: center;
     background-color: red;
 }
 
 .carousel_text_img_slide2{
     position: absolute;
      top: 20%;
      left: 20%;
      width: 60%;
      right: 20%;
      display: flex;
      justify-content: space-between;
      align-items: center;
  }
 /*/ IMGES SLIDE  /*/ 
.images_slide{
    height: 80vh;
    width: 100vw;
    object-fit: cover;

}

/*/  accueil /*/

.cont_status_ok{

    width: 100%;
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    border-top: solid 1px rgba(255, 255, 255, 0.219);
    border-bottom: solid 1px rgba(255, 255, 255, 0.219);
}
.bulle_accueil{
    background-color: rgba(230, 241, 230, 0.096);
    width: 10vw;
    height: 10vw;
    border-radius: 100%;
    border: solid 1px rgba(255, 255, 255, 0.192);
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 1rem;
    color: aliceblue;
}
/*/---------------/*/

@media screen and (max-width : 1050px){
    .carousel_text{
        width: 350px;
    }
}
@media screen and (max-width : 875px){ 
    .cont_img_slide,
    .carousel_img{
        display: none;
    }

    .carousel_text{
        display: flex;
        flex-direction: column;
        align-items: center;
        text-align: center;
        width: 600px;
    }
    .lien_contact{
        padding: 0rem;
        width: 100%;
        display: flex    ;
        justify-content: center;
    }
    .nom_lien_contact{
        font-size: 0.7rem;
    }

    /*/MENU  carousel /*/
.carousel_text_img_slide{
    position: absolute;
     top: 50%;
     left: 20%;
     width: 90%;
     left: 5%;
     display: flex;
     justify-content: center;
 }
 
 .carousel_text_img_slide2{
    position: absolute;
     top: 40%;
     left: 20%;
     width: 90%;
     left: 5%;
     display: flex;
     justify-content: center;
 }

}
