/*/  CHERCHE  et AJOUT /*/
.CT_inp_rech{
    width: 70vw;
    display: flex;
    align-items: center;
    margin: 20px;
    justify-content: space-between;   

}

.CT_btn_ad{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.CT_add_cont{
    height: 50px;
    width : 50px;
    border-radius: 100%;
    border: solid 5px firebrick;
    display: flex;
    justify-content: center;
    align-items: center;
}
.CT_plus_ajout{
    color: rgba(178, 34, 34, 0.651);
    font-size: 1.4rem;
    
}
.CT_ajout{
    color: rgba(178, 34, 34, 0.651);
}
.CT_search {

   
  }
  
  .CT_search > div {
    display: inline-block;
    position: relative;
    filter: drop-shadow(0 1px rgba(0, 0, 0, 0.473));
  }
  
  .CT_search > div:after {
    content: "";
    background: rgba(0, 0, 0, 0.473);
    width: 4px;
    height: 20px;
    position: absolute;
    top: 20px;
    right: -7px;
    transform: rotate(125deg);
  }
  
  .CT_search > div > input {
    color: rgba(0, 0, 0, 0.473);
    font-size: 10px;
    background: transparent;
    width: 15px;
    height: 15px;
    padding: 5px;
    border: solid 3px rgba(0, 0, 0, 0.473);
    outline: none;
    border-radius: 35px;
    transition: width 0.5s;
  }
  
  .CT_search > div > input::placeholder {
    color: rgba(0, 0, 0, 0.473);
    opacity: 0;
    transition: opacity 150ms ease-out;
  }
  
  .CT_search > div > input:focus::placeholder {
    opacity: 1;
  }
  
  .CT_search > div > input:focus,
  .CT_search > div > input:not(:placeholder-shown) {
    width: 250px;
  }

   
 .CT_delete{
    display: flex;
    justify-content: flex-end;
    font-size: 1.3rem;
    color: aliceblue;
 }
 
 .CT_delete:hover{
    color: rgba(176, 190, 202, 0.836);
 }
  
  @media screen and (max-width : 1180px){
    .CT_carr_cont{
        padding: 1rem;
        width: 320px;
    }
    .CT_inp_rech{
        width: 65vw;
        display: flex;
        flex-direction: column;
        align-items: flex-end;
        margin: 20px;
        justify-content: space-between;
        position: relative;

    }
    .CT_btn_ad{
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        margin-right: 2rem;
        margin-left: 50vw;
        margin-bottom: 1rem;
        position: absolute;
        top : -30% ;
        left: -90%;
   
    }
    .CT_add_cont{
        height: 30px;
        width : 30px;
        border-radius: 100%;
        border: solid 5px firebrick;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .CT_search > div > input:focus,
    .CT_search > div > input:not(:placeholder-shown) {
      width: 150px;
    }
    .CT_ajout{
        color: rgba(178, 34, 34, 0.651);
        margin-left: 1rem;
        font-size: 0.6rem;
    }

.CT_carr_cont5_mail,
.CT_carr_cont3_mail,
.CT_carr_cont5,
    .CT_carr_cont7{
