.PR_container_contact{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 2rem;
}

.PR_inp_rech{
   width: 70vw;
   display: flex;
   align-items: center;
   margin: 20px;
   justify-content: space-between;  
}
.PR_btn_ad{
   display: flex;
   flex-direction: column;
   justify-content: center;
   align-items: center;
}

.PR_add_cont{
   height: 50px;
   width : 50px;
   border-radius: 100%;
   border: solid 5px firebrick;
   display: flex;
   justify-content: center;
   align-items: center;
}
.PR_ajout{
   color: rgba(178, 34, 34, 0.651);
}

.PR_search {

  
 }
 
 .PR_search > div {
   display: inline-block;
   position: relative;
   filter: drop-shadow(0 1px rgba(0, 0, 0, 0.473));
 }
 
 .PR_search > div:after {
   content: "";
   background: rgba(0, 0, 0, 0.473);
   width: 4px;
   height: 20px;
   position: absolute;
   top: 20px;
   right: -7px;
   transform: rotate(125deg);
 }
 
 .PR_search > div > input {
   color: rgba(0, 0, 0, 0.473);
   font-size: 10px;
   background: transparent;
   width: 15px;
   height: 15px;
   padding: 5px;
   border: solid 3px rgba(0, 0, 0, 0.473);
   outline: none;
   border-radius: 35px;
   transition: width 0.5s;
 }
 
 .PR_search > div > input::placeholder {
   color: rgba(0, 0, 0, 0.473);
   opacity: 0;
   transition: opacity 150ms ease-out;
 }
 
 .PR_search > div > input:focus::placeholder {
   opacity: 1;
 }
 
 .PR_search > div > input:focus,
 .PR_search > div > input:not(:placeholder-shown) {
   width: 250px;
 }

