     color: #00224494;;
 }
 

.log_avec_accueil{
    height: 10px;
    width: 10px;
    border-radius: 100%;
    padding: 1px;
    margin-bottom: 5px;
    margin-left: 5px;
    background-color: #C81D11;
}
.B_navbar{
    display: none;
}

.Dlien_nav_bar{
    text-decoration: none;
    display: flex;
    justify-content: center;
