.ST_form_cont{
    width: 450px;
    height: 600px;
    margin-bottom: 2rem;
    display: flex;
    margin-top: 2rem;
    box-shadow: rgba(0, 0, 0, 0.4) 0px 2px 4px, rgba(0, 0, 0, 0.3) 0px 7px 13px -3px, rgba(0, 0, 0, 0.2) 0px -3px 0px inset;
}
.ST_ligne_cont{
    display: flex;
    margin-top: 2rem;
    margin-bottom: 2rem;
    width: 400px;
    margin-left: 30px;
    flex-direction: column;
}

/*/ PARTIE INPUT  DEBUT  /*/
.ST_container_input{
    display: flex;
    margin: 1rem;

  }
  .ST_text_input{
   margin-top: 1rem;
   color: rgba(0, 0, 0, 0.473);
   font-size: 1.2rem ;
   width: 160px;
   display: flex ;
   justify-content: space-between;   
  }
  
  .ST_input_write{
      width: 200px  ;
      position: relative;
      height: 40px;
      overflow: hidden;
      margin-left: 1rem;
   }
   .fa-search{
     color: #0f63d6;
   }
   .ST_input_write_2{
   background-color: #d60f0f00;
   font-weight: bold;
   margin-left: 35px;
   }
  
   
   .ST_input_write input{
     width: 100%;
     height: 100%;
     color: rgba(0, 0, 0, 0.473);
     border: none;
     outline: none;
   } 
   .ST_input_write label {
     position: absolute;
     bottom: 1px;
     left: 0%;
     width: 200px;
     height: 100%;
     pointer-events: none;
     border-bottom: 1.5px solid rgba(0, 0, 0, 0.473);   
   }
   .ST_input_write label::after{
     content: "";
     position: absolute;
     height: 100%;
     width: 200px;
     left: 0px;
     bottom: -1px;
     border-bottom: 2px solid rgba(0, 0, 0, 0.473);
     transform: translateX(-100%);
     transition: transform 0.3s ease;
   }
   .ST_input_write_4{
     position: absolute;
     bottom: 1px;
     left: 3px;
     transition: all 0.3s ease;
     color: rgba(0, 0, 0, 0.473);
   }
   
   .ST_input_write input:focus +.ST_input_write_3 .ST_input_write_4 , 
   .ST_input_write input:valid + .ST_input_write_3 .ST_input_write_4 {
     transform: translateY(-50%) translateX(-30%);
     font-size: 0.8em;
     color: rgba(0, 0, 0, 0.473);
   }
   
   .ST_input_write input:focus +.ST_input_write_3::after ,.ST_input_write input:valid + .ST_input_write_3::after{
     transform: translateX(0%);
   }
  
   .ST_btn_envoyer{
    background-color: #005C97;
    width: 150px;
    margin: auto;
    margin-top: 2rem;
    display: flex;
    justify-content: center;
    padding: 1rem;
    color: aliceblue;
   }
   /*/PARTIE INPUT FIN /*/

