.MT_form_cont{
    width: 420px;
    height: 450px;
    margin-bottom: 2rem;
    display: flex;
    margin-top: 2rem;
    box-shadow: rgba(0, 0, 0, 0.4) 0px 2px 4px, rgba(0, 0, 0, 0.3) 0px 7px 13px -3px, rgba(0, 0, 0, 0.2) 0px -3px 0px inset;
}
.MT_ligne_cont{
    display: flex;
    margin-top: 2rem;
    margin-bottom: 2rem;
    width: 400px;
    margin-left: 30px;
    flex-direction: column;
}

/*/ PARTIE INPUT  DEBUT  /*/
.MT_container_input{
    display: flex;
    margin: 1rem;
  
 
  }
  .MT_text_input{
   margin-top: 1rem;
   color: rgba(0, 0, 0, 0.473);
   font-size: 1.2rem ;
   width: 120px;
   display: flex ;
   justify-content: space-between;

  }
  
  .MT_input_write{
      width: 200px  ;
      position: relative;
      height: 40px;
      overflow: hidden;
      margin-left: 1rem;
   }
   .fa-search{
     color: #0f63d6;
   }
   .MT_input_write_2{
   background-color: #d60f0f00;
   font-weight: bold;
   margin-left: 35px;
   }
  
   .MT_input_write input{
     width: 100%;
     height: 100%;
     color: rgba(0, 0, 0, 0.473);
     border: none;
     outline: none;
   } 
   .MT_input_write label {
     position: absolute;
     bottom: 1px;
     left: 0%;
     width: 200px;
     height: 100%;
     pointer-events: none;
     border-bottom: 1.5px solid rgba(0, 0, 0, 0.473);   
   }
   .MT_input_write label::after{
     content: "";
     position: absolute;
     height: 100%;
     width: 200px;
     left: 0px;
     bottom: -1px;
     border-bottom: 2px solid rgba(0, 0, 0, 0.473);
     transform: translateX(-100%);
     transition: transform 0.3s ease;
   }
   .MT_input_write_4{
     position: absolute;
     bottom: 1px;
     left: 3px;
     transition: all 0.3s ease;
     color: rgba(0, 0, 0, 0.473);
   }
   
   .MT_input_write input:focus +.MT_input_write_3 .MT_input_write_4 , 
   .MT_input_write input:valid + .MT_input_write_3 .MT_input_write_4 {
     transform: translateY(-50%) translateX(-30%);
     font-size: 0.8em;
     color: rgba(0, 0, 0, 0.473);
   }
   
   .MT_input_write input:focus +.MT_input_write_3::after ,.MT_input_write input:valid + .MT_input_write_3::after{
     transform: translateX(0%);
   }
  
   .MT_btn_envoyer{
    background-color: #005C97;
    width: 150px;
    margin: auto;
    margin-top: 2rem;
    display: flex;
    justify-content: center;
    padding: 1rem;
    color: aliceblue;
   }
   /*/PARTIE INPUT FIN /*/

   @media screen and (max-width : 1180px){
    .MT_form_cont{
